.navbar {
    width: 95vw;
    display: flex;
    justify-content: space-between;
    margin: auto;
    padding-top: 20px;
}

.logo {
    display: flex;
    align-items: center;
    color: #fff;
}

.logo img {
    height: 40px;
    width: 5px;
    margin: 0.25rem;
}

.menu {
    margin: 10px;
    padding-top: 10px;
}

.icon {
    height: 18px;
    width: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.bar1, .bar2, .bar3 {
    height: 2px;
    width: 100%;
    background-color: #fff;
}

.bar1 {
    width: 70%;
    margin-left: 5px;
}

.bar3 {
    width: 60%;
    margin-left: 20%;
}


